
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
html, body {
  width: 100%;
  height: 100%;
  overflow: hidden;
  font-size: 16px;
}
a {
  position: relative;
  text-decoration: none;
  color: #2F51B5;
  cursor: pointer;
}
a::after {
  background-color: #2F51B5;
}
a:hover::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
}
#app {
