    display: -ms-flexbox;
    display: flex;
}
#app .asidebar .content-aside {
      -webkit-box-flex: 0;
          -ms-flex: 0 0 180px;
              flex: 0 0 180px;
      height: 100%;
      background: rgba(115, 115, 115, 0.9);
      color: #f1f1f1;
}
#app .asidebar .content-aside .aside-head {
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        width: 100%;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        height: 100px;
        margin-top: 10px;
}
#app .asidebar .content-aside .aside-content {
        width: 100%;
        -webkit-box-flex: 1;
            -ms-flex: 1;
