#app .content {
    position: relative;
    width: 100%;
    -webkit-box-flex: 9;
        -ms-flex: 9;
            flex: 9;
    overflow: auto;
}
#app .content .icon-loading {
      display: block;
      width: 35px;
      height: 35px;
      margin: 8px auto;
      background: url("./common/icons/loading.svg") no-repeat;
      background-size: contain;
      -webkit-animation: loading .6s linear infinite;
              animation: loading .6s linear infinite;
}
