}
#app .content .item .infos .lang span {
            display: inline-block;
            width: 12px;
            height: 12px;
            border-radius: 6px;
            background-color: #f1e05a;
            margin-right: 10px;
}
#app .content .item .infos .lang .C {
            background-color: #178600;
}
#app .content .item .infos .lang .TypeScript {
            background-color: #2b7489;
}
#app .content .item .infos .lang .Python {
