@media screen and (min-width: 760px) {
#app .content {
      width: 70%;
      margin-left: 15%;
      margin-right: 15%;
}
}
#app .about {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
#app .about .close {
      position: absolute;
      z-index: 3;
      display: inline-block;
      width: 30px;
      height: 30px;
      background: url("./common/icons/close.svg") no-repeat;
      background-size: contain;
      top: 11%;
      right: 12%;
}
@media screen and (min-width: 760px) {
#app .about .close {
        right: 26%;
}
}
#app .about .about-content {
      position: relative;
      z-index: 2;
      width: 80%;
      height: 80%;
      background-color: white;
      border-radius: 10px;
      padding: 30px 10px;
      box-shadow: 0 0 20px gray;
      overflow: auto;
}
#app .about .about-content h1 {
        text-align: center;
        font-size: 1.6rem;
        color: #31c27c;
        padding: 20px;
}
#app .about .about-content h2 {
        font-size: 1.2rem;
        text-align: center;
}
#app .about .about-content .skill, #app .about .about-content .url {
        text-indent: 1.2rem;
        margin-top: 16px;
        line-height: 2rem;
}
#app .about .about-content .skill b, #app .about .about-content .url b {
          display: inline-block;
}
#app .about .about-content div {
        padding-left: 60px;
        line-height: 1.5rem;
}
@media screen and (min-width: 760px) {
#app .about .about-content {
        width: 50% !important;
}
}
#app .about .mask {
      position: absolute;
      z-index: 0;
      width: 100%;
      height: 100%;
      background: none;
}
#app .showAbout-enter-active {
    transition: all .4s ease-in-out;
}
#app .showAbout-leave-active {
    transition: all 0.3s cubic-bezier(1, 0.5, 0.8, 1);
}
#app .showAbout-enter, #app .showAbout-leave-active {
    -webkit-transform: rotateZ(180deg);
            transform: rotateZ(180deg);
    opacity: 0;
}
#app .fadeIn-enter-active {
    transition: all .4s ease;
}
#app .fadeIn-leave-active {
    transition: all 0.2s cubic-bezier(1, 0.5, 0.8, 1);
}
#app .fadeIn-enter, #app .fadeIn-leave-active {
    -webkit-transform: translateX(-250px);
            transform: translateX(-250px);
    opacity: 0;
}
@-webkit-keyframes loading {
from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes loading {
from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}



